$ModerateBlue: hsl(238, 40%, 52%);
$SoftRed: hsl(358, 79%, 66%);
$LightGrayishBlue: hsl(239, 57%, 85%);
$Palered: hsl(357, 100%, 86%);

// ### Neutral

$DarkBlue: hsl(212, 24%, 26%);
$GrayishBlue: hsl(211, 10%, 45%);
$LightGray: hsl(223, 19%, 93%);
$VeryLightGray: hsl(228, 33%, 97%);
$White: hsl(0, 0%, 100%);


*{
	margin: 0;
	padding: 0;
	box-sizing: content-box;
}
html, body{
	top:0;
	position: relative;
	width:100%;
	height: 100%;
}
body{
	background-color: $LightGray;
}
body::-webkit-scrollbar {
	width: 1vw;
	
  }
  
body::-webkit-scrollbar-track {
	box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
	background-color: #f9f9fd;
	border-radius: 10px;
  }
  
body::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background: linear-gradient(180deg, #d396f7, #a000ea);
  }

.app-container{
	width:100%;
	height: 100%;
}
.roller{
	position: fixed;
	left: 0;
	top:-20px;
	height: 100vh;
	width: 150px;
	background-color: rgba(129, 129, 129, 0.0);
	&:hover{
		background-color: rgba(129, 129, 129, 0.3);
	}
}
.App {
	width: 500px;
	justify-content: center;
	align-content: center;
	margin: auto;
	margin-top: 65px;
	display: flex;
	flex-direction: column;
  	text-align: center;
}
.login{
	
	width: 500px;
	justify-content: center;
	align-content: center;
	margin: auto;
	margin-top: 105px;
	display: flex;
	flex-direction: column;
	
}

.body{
	flex-grow: 1;
	position: relative;
	display: flex;
	flex-direction: column;
	max-height: auto;
	margin: 10px;
	padding: 20px;
	border-radius: 15px;
	background-color: #fff;
	z-index: 1;
}
.UpTools{
	padding: 20px;
	height: 30px;
	background-color: aquamarine;
	flex: 0 0 12%;
}
.photos{
	position: relative;
	block-size: fit-content;
	flex: 0 0 70%;
	display: grid;
	grid-template: 1fr/ repeat(3, minmax(50px, 1fr));
	grid-auto-flow: dense;
	column-gap: 5px;
	row-gap: 5px;
	box-sizing: content-box;
	flex-direction: column;
	max-height: 256px;
	max-width: 440px;
	margin-bottom: 20px;
}
.photo__container{
	position: relative;
	display: block;
	max-height: 100px;
}
.photos img{

	width: 100%;
	height: 100%;
	object-fit: cover;
	margin-bottom: 3px;
	background-color: white;
	// object-fit: scale-down;
}
.description{
	flex: 0 0 7%;
	margin-bottom: 20px;
	max-width: 460px;
}
.downTools{
	flex: 0 0 7%;
}


.post-enter {
	transform: translateX(0px);

}
.post-enter-active {
	transform: translateX(0px);
	transition: all 500ms ease-in;
}
.post-exit {
	opacity: 1;
}
.post-exit-active {
	transform: translateX(-350px);
	transition: all 500ms ease-in;
}


@media (max-width:767px) {
	.App {
		max-width: 95%;
	}
}